module.exports = "<template>\n  <TableFor :records=\"records\">\n    <!-- Overrides the default first_name\n         header column -->\n    <th slot=\"first_name\" style=\"color: red\">\n      <i class=\"fa fa-users\"></i>\n    </th>\n\n    <template slot-scope=\"{ record }\">\n      <td name=\"id\" :header=\"false\">\n        <a :href=\"`/records/${record.id}`\"\n           @click='linkClicked'>\n          <i class=\"fa fa-edit\"></i>\n        </a>\n      </td>\n      <td name=\"email\" header=\"Login\" >\n      <td name=\"first_name\" />\n    </template>\n  </TableFor>\n</template>\n\n<script>\nexport default {\n  props: {\n    records: Array\n  },\n  methods: {\n    linkClicked (event) {\n      event.preventDefault()\n      alert('Link Clicked')\n    }\n  }\n}\n</script>\n"